import React from 'react';
import Link from 'next/link';
import 'isomorphic-fetch';

class Podcast extends React.Component{
    
    static async getInitialProps({query}){
        const podcastId = query.id;
        const podcast = await (await fetch(`https://api.audioboom.com/audio_clips/${ podcastId }.mp3`)).json();
        return{ podcastId }
    }
    
    render(){
        return(
            <div>
                <header>
                    <Link href={`/channel?id=${1234}`}>
                        <a className='close'>&lt; Volver</a>
                    </Link>
                    Podcasts
                </header>

                


                <style jsx>{`
                    header {
                        color: #fff;
                        background: #8756ca;
                        padding: 15px;
                        text-align: center;
                        position: relative;
                    }
                    a{
                        color: #FFF;
                        text-decoration: none;
                        position: absolute;
                        left: 15px; 
                        top: 50%;
                        transform: translateY(-50%);
                    }
                `}
                </style>
                <style jsx global>{`
                    body {
                    margin: 0;
                    font-family: system-ui;
                    background: white;
                    }
                `}</style>
            </div>
        )
    }
}

export default Podcast;